@import url(https://fonts.googleapis.com/css?family=Droid+Sans+Mono);
* {
	box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{text-align:center;display:flex;flex-direction:column;position:relative;width:100%;min-height:100vh;justify-content:center;align-items:center;background-color:#FBAB7E;background-image:linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%)}

header{display:flex}header h1{font-family:'Droid Sans Mono';font-weight:bold;text-transform:uppercase;letter-spacing:0.1em;color:purple}

.flipClock{display:flex;justify-content:space-between;width:500px}

.flipUnitContainer{display:block;position:relative;width:140px;height:120px;perspective-origin:50% 50%;perspective:300px;background-color:#fff;border-radius:3px;box-shadow:0px 10px 10px -10px grey}

.upperCard,.lowerCard{display:flex;position:relative;justify-content:center;width:100%;height:50%;overflow:hidden;border:1px solid #f5f5f5}.upperCard span,.lowerCard span{font-size:5em;font-family:"Droid Sans Mono",monospace;font-weight:lighter;color:#333}.upperCard{align-items:flex-end;border-bottom:0.5px solid #f5f5f5;border-top-left-radius:3px;border-top-right-radius:3px}.upperCard span{transform:translateY(50%)}.lowerCard{align-items:flex-start;border-top:0.5px solid #f5f5f5;border-bottom-left-radius:3px;border-bottom-right-radius:3px}.lowerCard span{transform:translateY(-50%)}

@keyframes fold{0%{transform:rotateX(0deg)}100%{transform:rotateX(-180deg)}}@keyframes unfold{0%{transform:rotateX(180deg)}100%{transform:rotateX(0deg)}}.AnimatedFlipCard{display:flex;justify-content:center;position:absolute;left:0;width:100%;height:50%;overflow:hidden;-webkit-backface-visibility:hidden;backface-visibility:hidden}.AnimatedFlipCard span{font-family:"Droid Sans Mono",monospace;font-size:5em;font-weight:lighter;color:#333}.AnimatedFlipCard.unfold{top:50%;align-items:flex-start;transform-origin:50% 0%;transform:rotateX(180deg);background-color:#fff;border-bottom-left-radius:3px;border-bottom-right-radius:3px;border:0.5px solid #f5f5f5;border-top:0.5px solid #f5f5f5}.AnimatedFlipCard.unfold span{transform:translateY(-50%)}.AnimatedFlipCard.fold{top:0%;align-items:flex-end;transform-origin:50% 100%;transform:rotateX(0deg);background-color:#fff;border-top-left-radius:3px;border-top-right-radius:3px;border:0.5px solid #f5f5f5;border-bottom:0.5px solid #f5f5f5}.AnimatedFlipCard.fold span{transform:translateY(50%)}.AnimatedFlipCard.fold{animation:fold .6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;transform-style:preserve-3d}.AnimatedFlipCard.unfold{animation:unfold .6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;transform-style:preserve-3d}

